
import { defineComponent} from 'vue';
import Pagination from '@/shared/components/Pagination/Pagination.vue';
import { loadingService } from '@/shared/services/LoadingScreenService';
import { COM21200111_BODY, COM21200111_RES, IB_ROLE_LIST } from '@/shared/type/COM21200111.type';
import { COMMON_KEY_VALUE } from '@/shared/constants/common.const';
import { modalController } from "@ionic/vue";

const loading = new loadingService();

export default defineComponent({
    name: "com207003sc00",
    components: {
        Pagination,
    },

    data() {
        return {
            dataTable:[] as IB_ROLE_LIST[],
            searchField:{
                userRoleID: "",
                userRoleStatusCode: "",
            },
            totalItems: 0,
			currentPage: 1,
			pageSize: 10,
			startingIndex: 1,
            COMMON_KEY_VALUE
        }
    },

    created() {
        this.inquiryRoleList()
    },

    computed:{
        headers(){
            return[
                { columnName: this.$t('com207003sc00.column.no_') },
                { columnName: this.$t('com207003sc00.column.view') },
                { columnName: this.$t('com207003sc00.column.role_id') },
                { columnName: this.$t('com207003sc00.column.role_name') },
                { columnName: this.$t('com207003sc00.column.role_des') },
                { columnName: this.$t('com207003sc00.column.status') },
            ]
        }
    },

    methods: {

        async inquiryRoleList() {
            loading.openloading()
            const body: COM21200111_BODY = {
                userRoleID:  this.searchField.userRoleID.trim(),
                userRoleStatusCode: this.searchField.userRoleStatusCode,
                pageSize: this.pageSize,
                pageNumber: this.currentPage
            }
            const res = await this.$request.getRequest('COM21200111', body, false) as COM21200111_RES;
            if(res.header.result){
                loading.closeLoading();
                this.dataTable = JSON.parse(JSON.stringify(this.mapDataTable(res.body.list)));
                this.totalItems = res.body.totalCount;
            } else {
                this.dialogMessage(res.header.error_text)
            }
        },

        handleRegister() {
            this.$router.push('/common-management/ib-role/register')
        },
        
        handleRowClick(index, rowData: IB_ROLE_LIST) {
            this.$router.push({
                path: `/common-management/ib-role/edit/${rowData.userRoleID}`,
                query: {
                    viewType:'detail',
                    userRoleID: rowData.userRoleID
                }
            });
        },

        handleSearch() {
            this.inquiryRoleList();
        },

        handleRefresh() {
            this.searchField.userRoleID = "";
            this.searchField.userRoleStatusCode = "";
            this.inquiryRoleList();
        },

        mapDataTable(data: IB_ROLE_LIST[]): IB_ROLE_LIST[] {
            const status = COMMON_KEY_VALUE.screenStatusCode;
   
            return data.map((item: IB_ROLE_LIST) => {
                return {
                    userRoleID:item.userRoleID,
                    userRoleName:item.userRoleName,
                    userRoleDescription:item.userRoleDescription,
                    userRoleStatusCode:item.userRoleStatusCode === status[0].key ? status[0].value : item.userRoleStatusCode === status[1].key ? status[1].value : status[2].value
                }
            })
        },

        handlePagination(pageNumber: number){
			this.currentPage = pageNumber
			this.inquiryRoleList()
		},

		handleStartingIndex(startingIndex: number){
			this.startingIndex = startingIndex
		},

        dialogMessage(message:string) {
            this.$dialog.onOpen({
                title: ``,
                content: `${message}`,
                showCancel: false,
                callback: () => {
                modalController.dismiss()
                }
            });
        },
    }
})
